.about__container {
    display: grid;
    grid-template-columns: 30% 60%;
    gap: 15%;
}

.about__me {
    width: 90%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h4 {
    font-size: 0.95rem;
}

.about__card h6 {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);

}

@media only screen and (max-width: 600px) {

    .about__me-image {
        display: none;
    }
    .about__container{
        gap:0;
    }
    .about__cards {
        grid-template-columns: repeat(1, 80%);
    }
}