header {
    margin: 0;
    height: 100vh;
}

.header__container {
    height: 100%;
    width: 100%;
    padding-top: 150px;
    text-align: center;
    position: relative;
    top: 20%;
}

.cta {
    margin-top: 10rem;
    padding: 5px;
    display: block;
    grid-gap: 1.5 rem;
    gap: 1.5 rem;
    justify-content:center;
}


.header__socials {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    position: absolute;
    top: 40%;
    left: 10%;
}



@media only screen and (max-width: 1025px) and (min-width: 601px) 
{
     header {
        height: 68vh;
    }

    .header__socials {
        display: none;
    }
}


@media only screen and (max-width: 600px) and (min-width: 1px) {
    header {
        height: 100vh;
    }

    .header__socials {
        display: none;
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem, 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    overscroll-behavior-block: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover {
    background: rgba(0, 0, 0, 0.5);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}


.about__container {
    display: grid;
    grid-template-columns: 30% 60%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 90%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h4 {
    font-size: 0.95rem;
}

.about__card h6 {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);

}

@media only screen and (max-width: 600px) {

    .about__me-image {
        display: none;
    }
    .about__container{
        grid-gap:0;
        gap:0;
    }
    .about__cards {
        grid-template-columns: repeat(1, 80%);
    }
}
.experience__container {
    padding-top: 2rem;
    min-width: 50%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}   

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    padding-left: 30%;
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}

@media only screen and (max-width: 600px) {
    .experience__container {
        flex-direction: column;
    }
}
.container {
    max-width: 100%;
}

.portfolio__container {
    min-width: 50%;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    text-align: center;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    display: inline;
    justify-content: center;
    align-content: center;
}


@media only screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: repeat(1, 80%);

    }
}

h3{
    padding: 10px 0 0 0;
}

h2 {
    padding: 10px;
}
.contact__container {
    padding: 100px 0;
    display: grid;
    justify-content: center;
    align-content: center;
}

.contact__options {
    display: flex;
    flex-direction: row;
    text-align: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    transition: var(--transition);
}

.contact_option {
    margin: 0.25rem;
    padding: 5rem 3.5rem;
    background-color: var(--color-bg-variant);
    border-radius: 2rem;

}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 2rem;
    margin-bottom: 0.1rem;
}

.gmail {
    color: #EA4335;
}

.fb {
    color:#4267B2;
}

.whatsapp {
    color: #25D366;
}

.contact__option a {
    margin-top: 1rem;
    display: inline-block;
    font-size: 1rem;
}

.contact__option h3 {
    color: var(--color-light);
}

@media only screen and (max-width: 600px) {
    .contact__options {
        flex-direction: column;
    }
}


* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #0A1612;
    --color-bg-variant: #2e598a;
    --color-primary: #4db5ff;
    --color-primary-variant: #fff;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 500ms ease; 

    --container-width-lg: 80%;
    --container-width-md: 85%;
    --container-width-ms: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
font-family: 'Nunito', sans-serif;
background: #0A1612;
background: var(--color-bg);
color: #fff;
color: var(--color-white);
line-height: 2.0;
}

/*======GENERAL STYLES=======*/

.container{
    width: 80%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 3rem;
    margin: 25px;
}

section {
    margin-top: 8rem;
    height: 100vh;

}

section > h2, section > h3 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a {
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 500ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    margin: 17px;
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    text-align: center;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 500ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #0A1612;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #0A1612;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/*======Media queries (Medium devices)=======*/

@media only screen and (max-width: 1025px) and (min-width: 601px) {
    .container {
        width: 85%;
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }

}

@media only screen and (max-width: 600px) and (min-width: 1px) {
    .container {
        width: 90%;
        width: var(--container-width-ms);
    }
    section > h2 {
        margin-bottom: 2rem;
    }

}
